.main {
    padding: 20px;
    height: 100vh;
}

.layout {
    display: flex;
}

.logo h6 {
    font-size: 1.5rem;
    text-align: center;
    margin: 20px 1px;
}

.role {
    color: white;
    font-size: 14px;
    margin-top: -20px;
}

.sidebar {
    background-color: #005555;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    margin-right: 20px;
    min-height: 100%;
    width: 300px;
    color: white;
}

.content {
    width: 100%;
    height: 100%;
}

.header {
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    margin-bottom: 20px;
    height: 10vh;
    /* display: flex;
    align-items: center;
    justify-content: space-between; */
}

.header_content {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 50px;
    margin: 0 15px;
}

.header_content i {
    margin-right: 20px;
    font-size: 1.2rem;
}

.header_content a {
    text-decoration: none;
    color: blue;
    margin: 0 10px;
    text-transform: uppercase;
}

.body {
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    height: 82vh;
    width: 100%;
    padding: 15px;
    overflow-x: scroll !important;
}

.menu {
    margin-top: 100px;
    padding: 0 10px;
}

.menu-item {
    margin-top: 30px;
}

.menu-item a {
    color: rgba(255, 255, 255, 0.727);
    text-decoration: none;
    font-size: 18px;
    padding: 0 10px;
}

.menu-item i {
    color: white;
    text-decoration: none;
    font-size: 18px;
    margin: 0 15px;
}

.active-menu-item {
    color: white;
    background-color: #013737;
    padding: 5px;
    border-radius: 5px;
}

.header-action-icon {
    font-size: 30px;
    color: black !important;
    cursor: pointer;
    padding-left: 10px;
}

.ant-scroll-number-only-unit {
    padding-right: -10px !important;
    padding-bottom: -10px !important;
}

@media screen and (max-width: 768px) {
    .menu {
        padding: 0 5px;
    }
}